/* Nav */
nav {
  position: sticky;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0.3em;
  height: 75px;
  width: 100%;
  background-color: var(--primary-color);
  padding: 0px 4%;
  z-index: 998;
}

.menu {
  display: none;
  min-height: 48px;
  min-width: 48px;
  font-size: 50.5px;
  color: var(--font-color);
}

span .fa {
  min-height: 44px;
  min-width: 44px;
}

span.fa.fa-bars,
span.fa.fa-bars::before {
  min-height: 44px;
  min-width: 44px;
}

.menu:hover {
  cursor: pointer;
}

.logo-font {
  font-size: x-large;
  font-weight: bold;
  color: var(--font-color);
  text-decoration: none;
  padding: 0.4em 0;
}

.nav-list {
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  flex-wrap: wrap;
  transition: ease;
}

.nav-item {
  box-sizing: border-box;
  line-height: 24px;
}

.nav-item a {
  padding: 0.7rem;
  display: inline-block;
  font-size: 1.3em;
  text-decoration: none;
  color: var(--font-color);
}

.nav-item button {
  padding: 0.9rem;
  background-color: transparent;
  border: 0 solid transparent;
  color: var(--font-color);
  cursor: pointer;
  font-size: 1.3;
  vertical-align: middle;
}

.nav-item a:hover {
  border-bottom: 1px solid var(--navbar-hover);
  cursor: pointer;
}

.nav-item button:hover {
  text-decoration: underline;
  color: var(--navbar-hover);
  cursor: pointer;
}

/* Footer */
/*
   * footer
   */

footer {
  padding: 12px;
  width: 100%;
  text-align: center;
  background-color: var(--primary-color);
}

footer ul {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  list-style: none;
}

footer li:nth-child(1) {
  font-size: 20px;
  font-weight: bold;
}

footer li {
  margin: 3px 0;
  font-size: 14px;
}
